<template>
    <div class="search-input-page">
        <div class="search-select">
            <div class="title">
                <div class="icon"><i class="el-icon-more"></i><br>FILTER</div>
                <p>排序結果</p>
            </div>
            <div class="select">
                <el-select v-model="value" filterable placeholder="篩選排序">
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
            </div>
        </div>
        <div class="search-form">
            <div class="title">
                <div class="icon"><i class="el-icon-search"></i><br>SEARCH</div>
                <p>搜尋車款</p>
            </div>
            <div class="select" :class="{'close': form}">
                <p>車種類型</p>
                <el-select v-model="value" filterable placeholder="請選擇">
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
                <p>車輛品牌</p>
                <el-select v-model="value" filterable placeholder="請選擇">
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
                <p>車款系列</p>
                <el-select v-model="value" filterable placeholder="請選擇">
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
                <p>價錢範圍：{{`${money[0]}～${money[1]} 萬` }}</p>
                <el-slider
                    range
                    v-model="money"
                    :marks="marks">
                </el-slider>
                <p>車輛狀態</p>
                <el-select v-model="value" filterable placeholder="請選擇">
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
                <p>車輛年/月份</p>
                <el-select v-model="value" filterable placeholder="請選擇">
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>
            </div>
            <div class="search-btn" :class="{'close': form}">
                <div class="car-btn" @click="openSearchInfo">搜尋車款</div>
            </div>
            <i class="el-icon-arrow-up" @click="openForm" :class="{'flip': form}"/>
        </div>
    </div>
</template>

<script>
export default {
    name: 'searchInput',
    data() {
        return {
            options: [{
                value: '1',
                label: '低價→高價'
                }, {
                value: '2',
                label: '高價→低價'
                }
            ],
            value: [],
            money: '',
            marks: { 0 : '100' , 100 : '1000' },
            form : false,
        }
    },
    methods: {
        openForm() {
            this.form = !this.form
        },
        openSearchInfo(){
            this.$router.push('/searchInfo')
        }
    }

}
</script>

<style lang="scss" scoped>
.search-input-page {
    .title {
        display: flex;
        color: #fff;
        height: 60px;
        text-align: center;
        align-items: center;
        .icon {
            background: #b04b23;
            padding: 10px;
            font-size: 12px;
            i {
                font-size: 24px;
                margin-bottom: 2px;
            }
        }
        p {
            background: #ca582a;
            text-align: center;
            width: 100%;
            line-height: 60px;
        }
    }
    .search-select {
        background: #ef6d38;
        margin-bottom: 15px;
        .select {
            padding: 10px;
        }
    }
    .search-form {
        background: #555555;
        font-size: 14px;
        .title {
            .icon {
                background: #333333;
            }
            p {
                background: #444444;
            }
        }
        .select {
            padding: 10px;
            color: #fff;
            height: calc(100vh - 450px);
            overflow: auto;
            p {
                margin: 25px 0 8px 25px;
            }
            
        }
        @media screen and (max-width: 768px){ 
            height: auto;
        }   
    }
    .search-btn {
        background: #444444;
        padding: 10px;
        .car-btn{
            font-size: 14px;
            width: 100%;
            height: 45px;
            line-height: 45px;
            margin:10px auto;
            border-radius: 50px;
        } 
    }
    .el-icon-arrow-up {
        background: #333333;
        color: #fff;
        font-size: 16px;
        width: 100%;
        padding: 5px 0;
        text-align: center;
        cursor: pointer;
    }
    
    .close {
        display: none;
        transition: all .5s;
    }
    .flip {
        transform: rotate(180deg);
    }
}
.select::-webkit-scrollbar-track
{
	background-color: #333333;
}

.select::-webkit-scrollbar
{
	width: 10px;
	background-color: #333333;
}

.select::-webkit-scrollbar-thumb
{
	background-color: #ef6d38;
}
</style>


